.FullWidthImage {
  position: relative;
  min-height: 400px;
  margin: 0 auto;
}

.FullWidthImage .content {
  width: 80%;
  max-width: 700px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.FullWidthImage .content img {
  width: 100%;
}


